import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

// 將 locales/__.json 中的內容取出來轉成 messages 的結構
// @returns {Object} VueI18n 實體的 messages 內容

function loadLocaleMessages() {
  // Webpack 引入一堆檔案的方法
  const locales = require.context(
    // 開始比對的目錄
    './locales',
    // 是否比對子目錄
    true,
    // 用正則表達式篩選檔案名稱，限定檔名為大小寫英數字、橫線底線逗號及空白的 JSON 檔
    /[A-Za-z0-9-_,\s]+\.json$/i

  )
  const messages = {}
  // 用每個檔案名稱去比對作為KEY
  locales.keys().forEach(key => {
    // console.log("Filename: ", key);
    const matched = key.match(/([A-Za-z0-9-_]+)\./i)
    // console.log("matched: ", matched);
    if (matched && matched.length > 1) {
      const locale = matched[1]
      messages[locale] = locales(key)
      // console.log("messages: ", messages);
    }
  })
  return messages
}
// 抓取瀏覽器語系
const language = window.navigator.userLanguage || window.navigator.language;
let lang
// console.log('language:',language)
if (language === 'zh-TW') {
   lang = 'zh-TW'
  // console.log('lang:',lang)
} else if (language === 'zh-CN') {
  lang = 'zh-CN'
  // console.log('lang:',lang)
} else if (language === 'en-US' || 'en') {
  lang = 'en'
  // console.log('lang:',lang)
}
// 指定 locale , fallbackLocale 和 messages
export default new VueI18n({
  locale: process.env.VUE_APP_I18N_LOCALE || lang,
  fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'zh-TW',
  messages: loadLocaleMessages()



})