import Global from "@/Global";
import axios from "axios";
import * as types from "../mutations_type";
import router from "@/router";
const CryptoJS = require('crypto-js');
import secret from '../../../utils/secret';
const state = {
    token:''
}



const actions = {
    userLogin({ commit }, data) {
        // console.log('actionsData', data)
         let encryptPw = secret.Encrypt(data.password);
     let decryptPw = secret.Decrypt(encryptPw);
        if (data.user == 'demo' && data.password == decryptPw)
        {
            commit(types.SETTOKEN, data)
            window.sessionStorage.setItem('user', data.user)

            // 要打開(暫時)
              router.push("/dashboard");
        }
        // axios.post().then(res => {

        // }).catch(error => {

        // })

        // window.location.assign("/");


    },
    userLogout({ commit }) {

        commit(types.DELTOKEN)
    }
}

const mutations = {
    [types.SETTOKEN](state, token) {
        state.token = token

        // window.location.assign("/dashboard#");
    },
    [types.DELTOKEN](state) {
        state.token = ''
        sessionStorage.removeItem('token')
        window.location.assign('/Login')
    }
};

const getters = {

};

export default {
    state,
    actions,
    mutations,
    getters,

};