import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'
Vue.use(Router)

// if (sessionStorage.getItem( 'token' )) {
// store.commit( 'SETTOKEN' , sessionStorage.getItem( 'token' ))
// }

const router = new Router({
  // mode: 'hash', // https://router.vuejs.org/api/#mode
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes()
})

function configRoutes () {
  return [
      {
      path: "/Login",
      name: 'Login',
        component: resolve =>
            require(["@/views/Login"], resolve)
    },
    {

      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      meta: {
          requireAuth: true,
         },
      component: resolve => require(['@/containers/TheContainer'], resolve),

      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: resolve => require(['@/views/Dashboard'], resolve)
        },

        {
          path: 'billing',
          redirect: '/billing/Overview',
          name: 'Billing',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'overview',
              name: 'Overview',
              component: resolve => require(['@/views/billing/Overview'], resolve),
            },
            {
              path: 'monthlybilling',
              name: 'Monthly Billing',
              component: resolve => require(['@/views/billing/MonthlyBilling'], resolve),
            },
            {
              path: 'dailybilling',
              name: 'Daily Billing',
              component: resolve => require(['@/views/billing/DailyBilling'], resolve),
            },
            {
              path: 'billingManagement',
              name: 'Billing Management',
              component: resolve => require(['@/views/billing/BillingManagement'], resolve),
            }
          ]
        },
        // {
        //   path: 'partner',
        //   redirect: '/partner/information',
        //   name: 'Partner',
        //   component: {
        //     render (c) { return c('router-view') }
        //   },
        //   children: [
        //     {
        //       path: 'information',
        //       name: 'Information',
        //       component: resolve => require(['@/views/partner/Information'], resolve),
        //     },
        //   ]
        // },
        {
          path: 'users',
          meta: {
            label: 'Users'
          },
          component: {
            render(c) {
              return c('router-view')
            }
          },
          children: [
            {
              path: 'ChangePw',
              name: 'Change Password',
              component: resolve => require(['@/views/users/ChangePw'], resolve),
            },
          ]
        },
         {
          path: 'support',
          name: 'Support',
          component: resolve => require(['@/views/support/List'], resolve),
        },

         {
          path: 'template',
          name: 'Template',
          component: resolve => require(['@/views/Template'], resolve)
        },
        {
          path: 'support',
          redirect: '/support/ListTest',
          name: 'Support',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'listtest',
              name: 'ListTest',
              component: resolve => require(['@/views/support/ListTest'], resolve),
            },
            {
              path: 'ticketcontent/:num',
              name: 'TicketContent',
              component: resolve => require(['@/views/support/TicketContent'], resolve),
            },
              {
              path: 'ticketcontent',
              name: 'TicketContent',
              component: resolve => require(['@/views/support/TicketContent'], resolve),
            },
            {
              path: 'addlist',
              name: 'AddList',
              component: resolve => require(['@/views/support/AddList'], resolve),
            },
            {
              path: 'awaitinglist',
              name: 'AwaitingList',
              component: resolve => require(['@/views/support/AwaitingList'], resolve),
            },
            {
              path: 'historylist',
              name: 'HistoryList',
              component: resolve => require(['@/views/support/HistoryList'], resolve),
            },
          ]
        },
        {
          path: 'automation',
          redirect: '/automation/BillingAlert',
          name: 'Support',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'billingalert',
              name: 'BillingAlert',
              component: resolve => require(['@/views/automation/BillingAlert'], resolve),
            },
            {
              path: 'partner',
              name: 'Partner',
              component: resolve => require(['@/views/automation/Partner'], resolve),
            },
          ]
        },
        {
          path: 'billingUpload/Upload',
          name: 'Billing Upload',
          component: resolve => require(['@/views/billingUpload/Upload'], resolve),
        },

      ]
    },
    {
      path: "*",
      name: 'Page404',
        component: resolve =>
            require(["@/views/Page404"], resolve)
    },


    // {
    //   path: '/pages',
    //   redirect: '/pages/404',
    //   name: 'Pages',
    //   component: {
    //     render (c) { return c('router-view') }
    //   },
    //   children: [
    //     {
    //       path: '404',
    //       name: 'Page404',
    //       component: resolve => require(['@/views/pages/Page404'], resolve),
    //     },
    //     {
    //       path: '500',
    //       name: 'Page500',
    //       component: resolve => require(['@/views/pages/Page500'], resolve),
    //     },
    //   ]
    // }
  ]
}
// 要打開(暫時)
router.beforeEach((to, from, next) => {
   const token = window.sessionStorage.getItem('user')
  if (to.matched.some(res => res.meta.requireAuth)) { // 判斷該路由是否需要登入許可權
    // console.log('token',token)
    if (token) { // 取當前的token是否存在
      // store.dispatch('token', token)
      next();
    }
    else {
      next(
        {
          name: 'Login'
          // path: '/login',
          // query: {redirect: to.fullPath} // 將跳轉的路由path作為引數，登入成功後跳轉到該路由
        })
    }
  }
  else {
    next();
  }
});
export default router;